<template>
  <div class="container bg-container">
    <Header :title="t('index.收益明細')" type="back"></Header>
    <div class="income">
      <div class="level-wrap">
        <div class="tab">
          <div class="tab-ink-bar">
            <span class="tab-inner"></span>
          </div>
          <div class="tab-item" :class="{'active':cid === item.id}" v-for="(item, index) in categoryList" :key="index" @click="tabChangeHandle(item)">{{ item.name }}</div>
        </div>
        <div class="tab-content">
          <div class="lists">
            <div class="border-out" v-for="(item,index) in lists" :key="index">
              <div class="border-in">
                <div class="li">
                  <div class="left">
                    <div class="content" v-if="cid == 0">{{item.address.substring(0, 8) + '...' + item.address.substring(item.address.length -8, item.address.length)}} {{item.layer}}L</div>
                    <template v-else>
                      <div class="content">{{t('index.左區')}} {{item.leftpv}}</div>
                      <div class="content">{{t('index.右區')}} {{item.rightpv}}</div>
                    </template>

                    <div class="time">{{item.time}}</div>
                  </div>
                  <div class="right">+{{item.money.split('.')[0]}}.<span class="small">{{item.money.split('.')[1]}} USDT</span></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
import { reactive, toRefs, computed, watch, nextTick, onMounted } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import { getReward } from "@/api"
export default {
  components: {
    Header
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts),
      web3: computed(() => store.state.provider),
      balance: 0,
      lists: [],
      categoryList: [
        {
          name: t('index.recommend'),
          id: 0
        }, {
          name: t('index.team'),
          id: 1
        }
      ],
      cid: 0,
      initContract: () => {
        data.initData()
      },
      initData: () => {
        data.getReward()
      },
      getReward: () => {
        data.lists = []
        getReward({ address: data.address, type: Number(data.cid) + 1 }).then((res) => {
          data.lists = res.data
        })
      },
      TabInkBarHandle: () => {
        let curTabItem = document.querySelectorAll('.tab .tab-item')[data.cid]
        let oTabInkBar = document.querySelector('.tab .tab-ink-bar')
        oTabInkBar.style.left = curTabItem.offsetLeft + (curTabItem.offsetWidth / 2) - (oTabInkBar.offsetWidth / 2) + 'px'
      },
      tabChangeHandle: async (item) => {
        console.log(item.id, 'item.id')
        data.cid = item.id
        data.loading = false
        data.page = 1
        data.finished = false
        data.getReward()
        await nextTick()
        data.TabInkBarHandle()
      },
    })
    onMounted(() => {
      data.TabInkBarHandle()
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.tab {
  height: 60px;
  display: flex;
  align-items: center;
  position: relative;
  flex-wrap: nowrap;
  overflow-x: auto;
  padding: 0 15px;
  &::-webkit-scrollbar {
    display: none;
  }

  .tab-item {
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    color: #000000;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.5);
    flex-shrink: 0;
    width: 50%;

    &.active {
      font-size: 17px;
      color: #fff;
    }
  }

  .tab-ink-bar {
    position: absolute;
    left: 0;
    bottom: 12px;
    width: 22px;
    height: 4px;
    background-color: transparent;
    transition: right 0.3s cubic-bezier(0.35, 0, 0.25, 1) 0.09s,
      left 0.3s cubic-bezier(0.35, 0, 0.25, 1);

    .tab-inner {
      border-radius: 0;
      display: block;
      margin: auto;
      height: 100%;
      background: linear-gradient(130deg, #447af4 25%, #33e7f7 100%);
      border-radius: 5px;
    }
  }
}
.income {
  border-radius: 20px 20px 0 0;
  .level-wrap {
    padding-bottom: 20px;
    .tab-content {
      font-size: 14px;
      margin: 0 15px;
      border-radius: 15px;
      overflow: hidden;
      .lists {
        .border-out {
          &:not(:last-child) {
            margin-bottom: 10px;
          }
        }
        .li {
          display: flex;
          align-items: center;
          padding: 10px 20px 10px 16px;
          flex-wrap: wrap;
          border-radius: 15px;
          .left {
            flex-shrink: 0;
            .content {
              font-size: 14px;
              font-weight: 600;
              padding-bottom: 7px;
            }
            .time {
              padding-top: 2.5px;
              font-size: 12px;
              color: #ccc;
            }
          }
          .right {
            text-align: right;
            margin-left: 10px;
            font-size: 16px;
            color: #43bb88;
            font-weight: 600;
            flex-grow: 1;
            .small {
              font-size: 12px;
            }
          }
        }
      }
    }
  }
}
</style>
<style lang="less">
</style>
